.home-page {
  position: relative;
  /* עלינו להוסיף את זה כדי שהכפתור יכול להיות ממוקם מעל התמונה */
  text-align: center;

  .background-image {
    width: 100%;
    object-fit: cover;
    object-position: center;
  }

  .welcome-text {
    margin-top: 16px;
    font-size: 24px;
  }

  .appointment-button {
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 7px 80px;
    font-size: 20px;
    text-decoration: none;
    color: rgb(61, 50, 50);
    border: 2px solid rgb(61, 50, 50);

    &:hover {
      color: rgb(61, 50, 50);
      border: 2px solid rgb(61, 50, 50);
    }
  }

