  .about-section {
    padding: 2rem;
    background-color: #f5f5f5;
    text-align: center;

    h4 {
      margin-bottom: 1rem;
      font-size: 2rem;
    }

    p {
      max-width: 800px;
      margin: 0 auto;
      font-size: 1.1rem;
    }
  }

  .contact-section {
    padding: 2rem;
    background-color: #e0e0e0;
    text-align: center;

    h4 {
      margin-bottom: 1rem;
      font-size: 2rem;
    }

    .contact-text {
      margin-bottom: 1rem;
      font-size: 1.1rem;
    }

    .contact-form {
      display: flex;
      flex-direction: column;
      gap: 1rem;
      max-width: 500px;
      margin: 0 auto;

      .submit-button {
        align-self: center;
        padding: 0.5rem 2rem;
      }
    }
  }
}